import styled from "../StyledComponents";
import { StyleProps, DivType } from "../styles/StyleTypes";
import { Div } from "../layout/Flex";
import { colorFromProps } from "../styles/Color";

export const applyDividerSize = (props: StyleProps) => {
  let size = 1;

  if (props.large) {
    size = 3;
  }
  if (props.vertical) {
    return `width: ${size}px; min-width: ${size};`;
  }
  return `height: ${size}px; min-height: ${size};`;
};

export const Divider = styled(Div)`
  ${props => applyDividerSize(props)}
  background-color: ${props => colorFromProps({...props, gray_50: true} as StyleProps,'')};
` as DivType;


