import { Theme } from "../themes/Theme";
import { ModifierNames } from "../ModifierNames";
import { ThemedStyledProps } from "styled-components/typings/styled-components";
import { StyleProps } from "./StyleTypes";

export const applyUtils = (props: StyleProps) => {

  return `
    ${cursor(props)}
    ${visible(props)}
    ${rounding(props)}
    ${rotation(props)}
    ${margin(props)}
    ${padding(props)}
    ${scrolling(props)}
    ${sizes(props)}
  `;
}


export const sizes = (props: StyleProps): string => {
  let style = "";
  if(props.fillParent) {
    style = 'width: 100%; height: 100%;'
  } else if (props.fillWidth) {
    style += "width: 100%;"
  } else if (props.fillHeight) {
    style += "height: 100%;";
  }
  return style;
};


export const scrolling = (props: StyleProps) =>  {
  if (props.scroll) {
    return 'overflow: scroll;'
  } else if (props.scrollY) {
    return 'overflow-y: scroll;'
  } else if (props.scrollX){
    return 'overflow-x: scroll;'
  }
  return '';
};

export const cursor = (props: StyleProps): string => {
  if (props.pointer) {
    return 'cursor: pointer;'
  }
  return '';
}

export const visible = (props: StyleProps) => {
  if (props.hidden) {
    return 'display: none;';
  }
  return '';
}

export const rounding = (props: StyleProps) => {
  if (props.round != undefined && props.round == false) return "";
  return `
    border-radius: ${props.round || props.theme.applyRounding ? props.theme.rounding : '0'};
  `;
};

export const rotation = (props: StyleProps) => {
  if(props.rotate_90) {
    return ` transform: rotate(90deg);`
  } else if (props.rotate_180) {
    return ` transform: rotate(180deg);`
  } else if (props.rotate_270) {
    return ` transform: rotate(270deg);`
  } else if (props.rotate_45) {
    return ` transform: rotate(45deg);`
  } else if (props.rotate_135) {
    return ` transform: rotate(135deg);`
  }  else if (props.rotate_225) {
    return ` transform: rotate(225deg);`
  } else {
    return '';
  }
};

