import * as React from "react";
import styled from "../StyledComponents";
import {colorFromProps, opaquer} from "../styles/Color";
import {StyleProps} from "../styles/StyleTypes";
import * as Ons from 'react-onsenui';
import { applyUtils } from "../styles/Util";

const rangeStyle = (props: StyleProps): string => {
  const color = colorFromProps(props, props.theme.accentColor);

  const css = `

    ${applyUtils(props)}

    .range__input {
        background: left center no-repeat;
        background-image: -webkit-linear-gradient(${color},${color});
        background-image: linear-gradient(${color},${color});
    }

    .range--material__focus-ring::-webkit-slider-thumb {
        box-shadow: 0 0 0 9px ${color};
        background-color: ${color};
    }
    
    .range--material__input::-webkit-slider-thumb {
        background-image: -webkit-radial-gradient(circle cover, ${color} 0, ${color} 6.6px, transparent 7px);
        background-image: radial-gradient(circle cover, ${color} 0, ${color} 6.6px, transparent 7px);
    }

    .range--material__input::-moz-slider-thumb {
        background-image: -webkit-radial-gradient(circle cover, ${color} 0, ${color} 6.6px, transparent 7px);
        background-image: radial-gradient(circle cover, ${color} 0, ${color} 6.6px, transparent 7px);
    }

    .range--material__focus-ring::-moz-slider-thumb {
        box-shadow: 0 0 0 9px ${color};
        background-color: ${color};
    }    

    .range--material__input::-ms-thumb {
        background-image: -webkit-radial-gradient(circle cover, ${color} 0, ${color} 6.6px, transparent 7px);
        background-image: radial-gradient(circle cover, ${color} 0, ${color} 6.6px, transparent 7px);
    }
  `
  console.log(css);
  return css;
}

export const Range = styled(Ons.Range)`
  ${props => rangeStyle(props)}
` as any;




