import * as React from "react";
import styled from "../StyledComponents";
import {colorFromProps, opaquer} from "../styles/Color";
import {StyleProps} from "../styles/StyleTypes";
import * as Ons from 'react-onsenui';
import { applyUtils } from "../styles/Util";

const progressBarStyle = (props: StyleProps): string => {

  const color = colorFromProps(props, props.theme.accentColor);
  const colorLight = opaquer(color, .2);
  
  return `

    ${applyUtils(props)}

    .progress-bar--material__primary,
    .progress-bar__primary {
      background-color: ${color};
    }

    .progress-bar__secondary,
    .progress-bar--material__secondary {
      background-color: ${colorLight};
    }    

    .progress-bar--indeterminate:after, .progress-bar--indeterminate:before {
      background-color: ${color};
    }

  `
}

export const ProgressBar = styled(Ons.ProgressBar)`
  ${props => progressBarStyle(props)}
` as any;


