import * as React from "react";
import {colorFromProps, autoColor, opaquer} from "../styles/Color";
import {StyleProps} from "../styles/StyleTypes";
import styled from "../StyledComponents";
import * as Ons from 'react-onsenui';
import { applyUtils } from "../styles/Util";

const fabStyle = (props: StyleProps): string => {
  const backgroundColor = colorFromProps(props, props.theme.accentColor);
  const color = autoColor(props, backgroundColor);
  const lighten = opaquer(backgroundColor, .81);
  
  return `

    ${applyUtils(props)}
    cursor:pointer;
    background-color: ${backgroundColor};
    color: ${color};

    &:active {
      background-color: ${lighten};
    }    

  `
}

export const Fab = styled(Ons.Fab)`
  ${props => fabStyle(props)}
` as any;




