import * as React from "react";
import styled from "../StyledComponents";
import { StyleProps, TextType, HeadingType, DivType } from "../styles/StyleTypes";
import { colorFromProps } from "../styles/Color";
import { margin, padding, visible, cursor, rotation, applyUtils } from "../styles/Util";
import { textAlign } from "../styles/Text";
import { Theme } from "../themes/Theme";

const flexDirection = (props: StyleProps):string => {
  if (props.column) {
    return 'column';
  } else if (props.rowReverse) {
    return 'row-reverse'
  } else if (props.columnReverse) {
    return 'column-reverse'
  }
  return 'row';
};

const flexWrap = (props: StyleProps):string => {
  if (props.flexWrap) {
    return 'wrap';
  } else if (props.wrapReverse) {
    return 'wrap-reverse'
  }
  return 'no-wrap';
};

const justifyContent = (props: StyleProps):string => {
  if (props.justifyEnd) {
    return 'flex-end';
  } else if (props.justifyCenter) {
    return 'center'
  } else if (props.justifySpaceBetween) {
    return 'space-between'
  } else if (props.justifySpaceAround) {
    return 'space-around'
  }
  return 'flex-start';
};

const alignItems = (props: StyleProps):string => {
  if (props.alignEnd) {
    return 'flex-end';
  } else if (props.alignCenter) {
    return 'center'
  } else if (props.alignBaseline) {
    return 'baseline'
  } else if (props.alignStart) {
    return 'flex-start'
  }
  return 'stretch';
};

const alignContent = (props: StyleProps):string => {
  if (props.alignContentEnd) {
    return 'flex-end';
  } else if (props.alignContentCenter) {
    return 'center'
  } else if (props.alignContentSpaceBetween) {
    return 'space-between'
  } else if (props.alignContentSpaceAround) {
    return 'space-around'
  } else if (props.alignContentSpaceAround)
  return 'flex-start';
};

const flexGrow = (props:StyleProps): any => {
  if (props.stretch) {
    return 1;
  }
  return 0;
};

const alignSelf = (props: StyleProps):string => {
  if (props.alignSelfEnd) {
    return 'flex-end';
  } else if (props.alignSelfCenter) {
    return 'center'
  } else if (props.alignSelfBaseline) {
    return 'baseline'
  } else if (props.alignSelfStart) {
    return 'flex-start'
  } else if (props.alignSelfStretch) {
    return 'stretch';
  }
  return 'auto';
};

export const flexItem = (props:StyleProps):string => {
  return `
    flex-grow: ${flexGrow(props)};  
    align-self: ${alignSelf(props)};
  `;
};



export const flexContainer = (props:StyleProps):string => {
  return `
    display: flex;
    flex-direction: ${flexDirection(props)};    
    flex-wrap: ${flexWrap(props)};
    justify-content: ${justifyContent(props)};
    align-items: ${alignItems(props)};
    align-content: ${alignContent(props)};
  `;
};

export const Div = styled.div`
  background-color: ${props => colorFromProps(props, 'transparent')};
  text-align: ${props => textAlign(props)};
  ${props => flexItem(props)}
  ${props => applyUtils(props)}
` as DivType;

export const Flex = styled(Div)` 
  ${props => flexContainer(props)}
` as DivType;

export const Row = (props: any) => {
  return (
    <Flex flexWrap {...props}>
      {props.children}
    </Flex>
  )
};

export const Column = (props: any) => {
  return (
    <ColumnStyle flexWrap {...props}>
      {props.children}
    </ColumnStyle>
  )
};

export const ColumnStyle = styled(Flex)`
  flex-basis: 100%;  
  
  @media screen and (min-width: ${props => props.theme.mobileBreakWidth}px) {
     & {
      flex: ${props => (props as any).width || 1};
    }
  }   
  
`as DivType;

export const JustifiedRow = styled(Flex)`

  * {
    flex-basis: 0;
  }

  >:first-child {
    text-align: left;
  }

  > :not(:first-child):not(:last-child) {
    text-align:center;
  }

  > :last-child {
    text-align: right;
  }

`


