import * as React from "react";
import { LabelledStyle } from "./LabelledStyle";
import { Row, Column } from "../layout/Flex";
import { Text } from "../styles/Text";

export const GridPreview = (props:any) => {
  return (
    <LabelledStyle name="Evenly Spaced Columns">
      <Row>
        <Column gray_20 justifyCenter pd_small pointer><Text small>Column 1</Text></Column>
        <Column gray_20 justifyCenter pd_small><Text small>Column 2</Text></Column>
      </Row>
      <Row center mt_medium>
        <Column gray_20 justifyCenter pd_small><Text small>Column 1</Text></Column>
        <Column gray_20 justifyCenter pd_small><Text small>Column 2</Text></Column>
        <Column gray_20 justifyCenter pd_small pointer><Text small>Column 3</Text></Column>
      </Row>            
    </LabelledStyle>    
  )
}


