import * as React from "react";
import styled from "../StyledComponents";
import {colorFromProps} from "../styles/Color";
import {StyleProps} from "../styles/StyleTypes";
import * as Ons from 'react-onsenui';
import { applyUtils } from "../styles/Util";

const iosOnlyStyle = (color: string) => {
  return `

  .radio-button__checkmark:after {    
    border: 2px solid ${color};
    border-top: none;
    border-right: none;
    border-radius: 0;
  }  
  `
}

export const radioStyle = (props: StyleProps): string =>  {

  const color = colorFromProps(props, props.theme.accentColor);
  const isNotMaterial = (props as any).modifier !== "material";
  
  return `

  ${isNotMaterial ? iosOnlyStyle(color): ''}

  ${applyUtils(props)}

  .radio-button--material__checkmark:before {    
    border: 2px solid ${props.theme.inputBorderColor};
  }

  .radio-button--material__input:checked:before {
      box-shadow: 0 0 0 14px ${color};
      background-color: ${color};
  }  

  > :checked+.radio-button--material__checkmark:after {
    background: ${color}; 
  }

    > :checked+.radio-button--material__checkmark:before {
        background: 0 0;
        border: 2px solid ${color};
    }

  `
}

export const Radio = styled(Ons.Radio)`    
  ${props => radioStyle(props)}
` as any;




