import * as React from "react";
import styled from "../StyledComponents";
import {colorFromProps, opaquer} from "../styles/Color";
import {StyleProps} from "../styles/StyleTypes";
import * as Ons from 'react-onsenui';
import { applyUtils } from "../styles/Util";
const progressCircularStyle = (props: StyleProps): string => {

  const color = colorFromProps(props, props.theme.accentColor);
  const colorLight = opaquer(color, .2);
  
  return `
    ${applyUtils(props)}
    
    .progress-circular__primary,
    .progress-circular--material__primary {
        stroke: ${color};
    }

    .progress-circular__secondary,
    .progress-circular--material__secondary {
        stroke: ${colorLight};
    }
  `
}

export const ProgressCircular = styled(Ons.ProgressCircular)`
  ${props => progressCircularStyle(props)}
` as any;


