import * as Color from "color";
import { StyleProps } from "./StyleTypes";

export const colorFromProps = (props: StyleProps, defaultValue: string) => {
  if (props.primary) {
    return props.theme.primaryTextColor;    
  } else if (props.secondary) {
    return props.theme.secondaryTextColor;
  } else if (props.accent) {
    return props.theme.accentColor 
  }  else if (props.brand) {
    return props.theme.brandColor;
  } else if (props.background) {
    return props.theme.backgroundColor;
  } else if (props.white) {
    return 'white';
  } else if (props.dark) {
    return props.theme.darkColor;
  } else if (props.gray) {
    return props.theme.grayColor;
  }

  const grayVariant = checkVariant('gray', props);
  if (grayVariant) return whiten(props.theme.grayColor, grayVariant);    

  const darkVariant = checkVariant('dark', props);
  if (darkVariant) return whiten(props.theme.darkColor, darkVariant);  

  const whiteVariant = checkVariant('white', props);
  if (whiteVariant) return opaquer(props.theme.whiteColor, whiteVariant);  

  return defaultValue;
};

export const checkVariant = (prefix: string, props: StyleProps) => {
  const _props = props as any;  
  for (let i = 90; i >=10; i-=10) {
    const prop = _props[`${prefix}_${i}`];
    if (prop) {      
      return i/100;
    }
  }
  return null;
}

export const whiten = (color: string, ratio: number): string => {
  return Color(color).mix(Color('white'), ratio).string();
}

export const opaquer = (color: string, alpha: number): string => {
  return Color(color).alpha(alpha).string();
}

export const autoColor = (props: StyleProps, contrastWith: string) => {
  return Color(contrastWith).dark() ? '#FFFFFF' : props.theme.primaryTextColor;
};


