import * as React from "react";
import { Flex } from "../layout/Flex";
import { Text } from "../styles/Text";

export interface StyleLabelProps {
  name: string;
  children: any;
}

export const LabelledStyle = (props: StyleLabelProps) => {
  return (
    <Flex column>
      <Text small gray_90 uppercase mt_small mb_tiny>{props.name}</Text>
      {props.children}
    </Flex>
  )
}


