import styled from "../StyledComponents";
import {colorFromProps, autoColor} from "../styles/Color";
import {StyleProps} from "../styles/StyleTypes";
import {BaseText,  fontStyle, textTransform, fontWeight, font, fontSize,} from "../styles/Text";
import * as Ons from 'react-onsenui';
import { opaquer } from "../Index";
import { applyUtils } from "../styles/Util";

export const textStyles = (props: StyleProps) => {
  return `  
    font-style: ${fontStyle(props)};
    text-transform: ${textTransform(props)};
    font-weight: ${fontWeight(props, 'normal')};
    font-family: ${font(props)};    
    font-size: ${fontSize(props, props.theme.baseTextSize)};  
  `  ;
}

const buttonStyle = (props: StyleProps): string => {

  const modifier = (props as any).modifier;
  const isOutline = modifier === "outline";
  const isHollow = modifier === "quiet" || isOutline;
  const color = colorFromProps(props, props.theme.accentColor);

  const backgroundColor = isHollow ? 'transparent' : color;
  const backgroundColorActive = isOutline ? opaquer(color, .3) : backgroundColor;
  const borderColor = modifier === "outline" ? color : 'rgba(0,0,0,0)';
  const textColor =  isHollow ? color :  autoColor(props, color);

  return `
    ${applyUtils(props)}
    ${textStyles(props)}
    cursor: pointer;
    background-color: ${backgroundColor};
    color: ${textColor};
    border-color: ${borderColor};

    &:active {
      background-color: ${backgroundColorActive};    
    }  
  `;
}

export const Button = styled(Ons.Button)`    
  ${props => buttonStyle(props)}
` as any;


