import * as React from "react";
import styled from "../StyledComponents";
import {colorFromProps, autoColor} from "../styles/Color";
import {StyleProps} from "../styles/StyleTypes";
import * as Ons from 'react-onsenui';
import { applyUtils } from "../styles/Util";

export const checkBoxStyle = (props: StyleProps):string => {
  const backgroundColor = colorFromProps(props, props.theme.accentColor);
  const color = autoColor(props, backgroundColor);
  
  return `  
    ${applyUtils(props)}
    
    input {
      cursor: pointer;
    }
  
    .checkbox--material__checkmark:before {
      cursor: pointer;
      border-color: ${props.theme.inputBorderColor};
    }

    > :checked+.checkbox__checkmark:before  {
      color: ${color};
      background-color: ${backgroundColor};
      border-color: ${backgroundColor};
    }     
  `
}

export const Checkbox = styled(Ons.Checkbox)`
  ${props => checkBoxStyle(props)}
` as any;




