import * as React from "react";
import { Text } from "../styles/Text";
import { LabelledStyle } from "./LabelledStyle";
import { Text1 } from "./PreviewText";

export const ParagraphPreview = (props: any) => {
  return (
    <div>
      <LabelledStyle name="Small text Primary">
        <Text small>{Text1}</Text>
      </LabelledStyle>    
      <LabelledStyle name="Regular Text Primary (Default)">
        <Text>{Text1}</Text>
      </LabelledStyle>    
      <LabelledStyle name="Large Text Primary">
        <Text large>{Text1}</Text>
      </LabelledStyle>    
      <LabelledStyle name="X-Large Text Primary">
        <Text xlarge>{Text1}</Text>
      </LabelledStyle>    
      <LabelledStyle name="X-Large Text Primary">
        <Text gigantic>{Text1}</Text>
      </LabelledStyle>          
      <LabelledStyle name="Bold Text Primary">
        <Text bold>{Text1}</Text>
      </LabelledStyle>          
      <LabelledStyle name="Italic Text Primary">
        <Text italic>{Text1}</Text>
      </LabelledStyle>                
      <LabelledStyle name="Bold Italic Text Primary">
        <Text bold italic>{Text1}</Text>
      </LabelledStyle>                 
      <LabelledStyle name="Regular Text Secondary">
        <Text secondary>{Text1}</Text>
      </LabelledStyle>        
      <LabelledStyle name="Regular Text Accent">
        <Text accent>{Text1}</Text>
      </LabelledStyle>              
      <LabelledStyle name="Regular Text Brand">
        <Text brand>{Text1}</Text>
      </LabelledStyle>                    
    </div>
  )
}


