import * as React from "react";
import styled from "../StyledComponents";
import {colorFromProps, autoColor} from "../styles/Color";
import {StyleProps} from "../styles/StyleTypes";
import * as Ons from 'react-onsenui';
import { textStyles } from "./Button";
import { applyUtils } from "../styles/Util";

const tabbarStyle = (props: StyleProps): string => {

  const isMaterial = (props as any).modifier === "material";
  const propsColor = isMaterial ? colorFromProps(props, props.theme.brandColor) : colorFromProps(props, props.theme.accentColor);
  const backgroundColor = isMaterial ? propsColor : 'transparent';
  const color  = isMaterial ? autoColor(props, propsColor) : propsColor;

  return `
  
    ${applyUtils(props)}

    button > div {
      ${textStyles(props)}
    }
    
    .tabbar--material {
      background-color: ${backgroundColor};
    }
  
    .tabbar--material__item:not([ripple]):active {
      background-color: ${backgroundColor};
    }    

    .tabbar--material__button:after {
      background-color: ${props.theme.accentColor};
    }    

    .tabbar__button {
      cursor: pointer;
    }

    input[type=radio]:checked + button {
      color: ${color};
    }
  `
}
export const Tabbar = styled(Ons.Tabbar)`
  ${props => tabbarStyle(props)}
` as any;




