// Override the default styled-components classes to allow support for custom themes
// See https://www.styled-components.com/docs/api#typescript

import * as styledComponents from 'styled-components';
import { ThemedStyledComponentsModule } from 'styled-components';
import {Theme} from './themes/Theme';

const {
    default: styled,
    css,
    injectGlobal,
    keyframes,
    ThemeProvider
} = styledComponents as ThemedStyledComponentsModule<any> as ThemedStyledComponentsModule<Theme>;

export { css, injectGlobal, keyframes, ThemeProvider };
export default styled;


