import * as React from "react";
import styled from "../StyledComponents";
import {colorFromProps, opaquer} from "../styles/Color";
import {StyleProps} from "../styles/StyleTypes";
import * as Ons from 'react-onsenui';
import { applyUtils } from "../styles/Util";


export const SpeedDial = styled(Ons.SpeedDial)`
` as any;

const speedDialItemStyle = (props: StyleProps): string => {

  const color = colorFromProps(props, props.theme.accentColor);
  const colorActive = opaquer(color, .8);

  return `

    ${applyUtils(props)}

    background-color: ${color};
    cursor: pointer;
    &:active {
      background-color: ${colorActive};
    }
  `
}

export const SpeedDialItem = styled(Ons.SpeedDialItem)`
  ${props => speedDialItemStyle(props)}
`;


