import * as React from "react";
import styled from "../StyledComponents";
import {css} from "../StyledComponents";
import {colorFromProps} from "../styles/Color";
import {StyleProps} from "../styles/StyleTypes";
import {BaseText} from "../styles/Text";
import * as Ons from 'react-onsenui';

import { textStyles } from "./Button";
import { applyUtils } from "../styles/Util";

const focusStyle = (isTransparent: boolean, color: string) => {

  if(isTransparent) {
    return `
      .text-input--material:focus {
        background: rgba(0,0,0,0);
      }
    `
  }

  return `
    .text-input--material:focus {
      background-image: -webkit-linear-gradient(${color},${color}),-webkit-linear-gradient(bottom,transparent 1px,#afafaf 1px);
      background-image: linear-gradient(${color},${color}),linear-gradient(to top,transparent 1px,#afafaf 1px);
    }     
  `
}

export const inputStyle = (props: StyleProps): string =>  {

  const isTransparent = (props as any).modifier === "transparent";
  const backgroundColor = isTransparent ? 'rgba(0,0,0,0)' : props.theme.inputBackgroundColor;  
  const color = colorFromProps(props, props.theme.accentColor);

  return `
        
    .text-input {            
      ${textStyles(props)}        
      ${applyUtils(props)}
      color: ${colorFromProps(props, props.theme.primaryTextColor)};      
      background-color: ${backgroundColor};
      height: auto;
    }     

    .text-input--material {
      background: ${isTransparent ? 'none' : `${backgroundColor}` } ;
    }
    
    ${focusStyle(isTransparent, color)}
  `  
}

export const Input = styled(Ons.Input)`
  ${props => inputStyle(props)}
` as any;




