import * as React from "react";
import styled from "../StyledComponents";
import {colorFromProps, whiten} from "../styles/Color";
import {StyleProps} from "../styles/StyleTypes";
import * as Ons from 'react-onsenui';
import { applyUtils } from "../styles/Util";

const switchStyle = (props: StyleProps): string => {

  const thumbColor = colorFromProps(props, props.theme.accentColor);
  const trackColor = (props as any).modifier === "material" ? whiten(thumbColor, .5) : thumbColor;

  return `  

    ${applyUtils(props)}

    > :checked+.switch__toggle {
        box-shadow: inset 0 0 0 2px ${trackColor};
        background-color: ${trackColor};
    }  

    > :checked+.switch--material__toggle>.switch--material__handle {
      background-color: ${thumbColor};
    }
  `
}

export const Switch = styled(Ons.Switch)`
  ${props => switchStyle(props)}
` as any;




