import * as React from "react";
import styled from "../StyledComponents";
import {colorFromProps, opaquer} from "../styles/Color";
import {StyleProps} from "../styles/StyleTypes";
import * as Ons from 'react-onsenui';
import { textStyles } from "./Button";
import { applyUtils } from "../styles/Util";

export const SearchInput = styled(Ons.SearchInput)`

    ${props => applyUtils(props)}

    .search-input {            
      padding-right: .8em;
      padding-top: .8em;
      padding-bottom: .8em;
      ${props => textStyles(props)}  
      color: ${props => colorFromProps(props, props.theme.primaryTextColor)};      
      height: auto;
    }
    .search-input--material {
      padding-right: 1em;
      padding-top: 1em;
      padding-bottom: 1em;
    }
` as any;




