import styled from "../StyledComponents";
import { StyleProps, TextType, HeadingType, SpanType, IType } from "./StyleTypes";
import { colorFromProps } from "./Color";
import { margin, padding, visible, cursor, rotation, applyUtils } from "./Util";
import { flexItem } from "../layout/Flex";

export const fontSize = (props: StyleProps, defaultSize = props.theme.baseTextSize) => {
  if (props.small) {
    return props.theme.smallTextSize;
  } else if (props.large) {
    return props.theme.largeTextSize;
  } else if (props.xlarge) {
    return props.theme.xlargeTextSize;
  } else if (props.gigantic) {
    return props.theme.giganticTextSize;
  }
  return defaultSize;
};

export const font = (props: StyleProps) => {
  if (props.headingFont) {
    return props.theme.headingFont;
  }
  return props.theme.paragraphFont;
};

export const fontWeight = (props: StyleProps, defaultWeight: number | string = props.theme.paragraphFontWeight) => {
  if (props.bold) {
    return 700
  } else if (props.light) {
    return 300;
  } else if (props.extraLight) {
    return 200
  } else if (props.thin) {
    return 100;
  } else if (props.semiBold) {
    return 600
  } else if (props.medium) {
    return 500;
  } else if (props.extraBold) {
    return 800;
  } else if (props.black) {
    return 900;
  }else if (props.regular) {
    return 400;
  }
  return defaultWeight;
};

export const fontStyle = (props: StyleProps) => {
  if (props.italic) {
    return 'italic'
  } else if (props.oblique) {
    return 'oblique';
  } else {
    return 'normal'
  }
};


export const textTransform = (props: StyleProps) => {
  if (props.capitalize) {
    return 'capitalize';
  } else if (props.uppercase) {
    return 'uppercase'
  }
  return 'none';
};

export const textAlign = (props: StyleProps) => {
  if (props.center) {
    return 'center';
  } else if (props.right) {
    return 'right'
  } else if (props.justified) {
    return 'justified'
  }
  return 'inherit';
};

const textDecoration = (props: StyleProps) => {
  if (props.underline) {
    return 'underline';
  } else if (props.overline) {
    return 'overline'
  } else if (props.strikethrough) {
    return 'strikethrough'
  }
  return 'none';
};

export const BaseText = (props: StyleProps): string => {
  return `
    margin: 0;
    color: ${colorFromProps(props, props.theme.primaryTextColor)};
    line-height: ${props.theme.lineHeight};
    max-width: ${props.theme.maxTextWidth};
    font-weight: ${fontWeight(props)};
    font-size: ${fontSize(props)};
    text-transform: ${textTransform(props)};
    text-decoration: ${textDecoration(props)};
    text-align: ${textAlign(props)};
    font-family: ${font(props)};    
    font-style: ${fontStyle(props)};
    ${applyUtils(props)}
    ${flexItem(props)}
  `
};

export const Text = styled.p`    
  ${props => BaseText(props)}      
` as TextType;

export const Heading = (props: StyleProps, size: string): string => {
  return `
    ${BaseText({...props, headingFont:true})}
    font-weight: ${fontWeight(props, props.theme.headingFontWeight)};
    font-size: ${fontSize(props, size)};
  `
};

export const H1 = styled.h1`
  ${props => Heading(props, props.theme.h1Size)}
` as HeadingType;

export const H2 = styled.h2`
  ${props => Heading(props, props.theme.h2Size)}
` as HeadingType;

export const H3 = styled.h3`
  ${props => Heading(props, props.theme.h3Size)}
` as HeadingType;

export const H4 = styled.h4`
  ${props => Heading(props, props.theme.h4Size)}
` as HeadingType;

export const H5 = styled.h5`
  ${props => Heading(props, props.theme.h5Size)}
` as HeadingType;

export const H6 = styled.h6`
  ${props => Heading(props, props.theme.h6Size)}
` as HeadingType;

export const IconBase = (props: StyleProps): string => {
 return `
    ${flexItem(props)}
    ${BaseText(props)}  
 ` 
}

export const SIcon = styled.span`
  ${props => IconBase(props)}
` as SpanType;

export const IIcon = styled.i`
  ${props => IconBase(props)} 
` as IType;

export const Icon = SIcon;

export const Span = styled.span`
  ${props => BaseText(props)}
` as SpanType;


