import * as React from "react";
import { Theme } from "../themes/Theme";
import { ThemeProvider } from "../StyledComponents";
import { Flex, Row, Column } from "../layout/Flex";
import { H1, Text } from "../styles/Text";
import { LabelledStyle } from "./LabelledStyle";
import { HeadingPreview } from "./HeadingPreview";
import { Divider } from "../components/Dividers";
import { ParagraphPreview } from "./ParagraphPreview";
import { SectionLabel } from "./SectionLabel";
import { GridPreview } from "./GridPreview";

export interface StylePreviewProps {
  theme: Theme;
}

export class StylePreview extends React.Component<StylePreviewProps, undefined> {
  constructor(props: StylePreviewProps) {
    super(props)
  }

  render() {
    const {theme} = this.props;
    return (
      <ThemeProvider theme={theme}>
        <Flex pd_medium>
          <Flex column>
            <SectionLabel name="Typography" />
            <HeadingPreview />          
            <Divider fillWidth mt_medium mb_medium/>
            <ParagraphPreview />
          </Flex>
          <Flex column stretch style={{minWidth: 700}}>
            <SectionLabel name="Grid & Layout" />
            <GridPreview />
            <Divider fillWidth mt_medium mb_medium/>
          </Flex>          
        </Flex>
      </ThemeProvider>
    )
  }
}


