import * as React from "react";
import styled from "../StyledComponents";
import * as Ons from 'react-onsenui';
import { applyUtils } from "../styles/Util";
import { StyleProps, colorFromProps, opaquer } from "../Index";

const rippleStyle = (props: StyleProps) => {
  const color = colorFromProps(props, "#FFFFFF");
  const colorOpaque = opaquer(color, .2);

  const result = `
    ${applyUtils(props)}

    .ripple__wave {
      background: ${colorOpaque};
    }
  `
  return result;
}

export const Ripple = styled(Ons.Ripple)`
  ${props => rippleStyle(props)}
` as any;




